@import "ScssHelpers/helpers_all";

//---------------------------------------------testimonials_section
.testimonials_section {
	background-color: var(--light_color);

	@include bp($point_1 + 81, min) {
		padding-top: 9rem;
		padding-bottom: 7rem;
	}

	@media (min-width: $point_4 + 1) and (max-width: $point_1 + 80) {
		padding-top: 7.2rem;
		padding-bottom: 6rem;
	}

	@media (min-width: $point_6 + 1) and (max-width: $point_4) {
		padding-top: 3.2rem;
		padding-bottom: 5rem;
	}

	@include bp($point_6) {
		padding-top: 3.8rem;
		padding-bottom: 3rem;
		// padding-bottom: 5.4rem;
	}
}

.testimonials_2_section {
	z-index: 2;
	background-color: var(--light_color);

	@include bp($point_1 + 81, min) {
		padding-top: 7rem;
		padding-bottom: 18rem;
	}

	@media (min-width: $point_4 + 1) and (max-width: $point_1 + 80) {
		padding-top: 6rem;
		padding-bottom: 12rem;
	}

	@media (min-width: $point_6 + 1) and (max-width: $point_4) {
		padding-top: 5rem;
		padding-bottom: 10rem;
	}

	@include bp($point_6) {
		padding-top: 3.8rem;
		padding-bottom: 0;
		// padding-bottom: 5.4rem;
	}
}

.testimonials_section__in {
	@include flex_block(row, wrap, flex-start);

	width: 100%;
}

.testimonials_section__title {
	width: 100%;

	font-family: $biotif_font;
	font-weight: 400;
	letter-spacing: -0.28px;
	color: var(--gray_color);

	@include bp($point_1 + 81, min) {
		margin-bottom: 5rem;

		font-size: 2.8rem;
		line-height: 1.29;
		letter-spacing: -0.28px;
	}

	@media (min-width: $point_4 + 1) and (max-width: $point_1 + 80) {
		margin-bottom: 4.2rem;

		font-size: 2.4rem;
		line-height: 1.25;
		letter-spacing: -0.24px;
	}

	@media (min-width: $point_6 + 1) and (max-width: $point_4) {
		margin-bottom: 1.2rem;

		font-size: 1.4rem;
		line-height: 2.57;
		letter-spacing: -0.14px;
	}

	@include bp($point_6) {
		margin-bottom: 3.2rem;

		font-size: 1.9rem;
		line-height: 1.32;
		letter-spacing: -0.19px;
	}
}

.testimonials_section__row {
	@extend %global_flex_block_row_nowrap_flex-start;

	@include bp($point_3) {
		flex-wrap: wrap;
	}
}

.testimonials_section__col {
	width: 100%;

	&.testimonials_section__col--client_testimoni_mod {
		position: relative;
		max-width: 104.4rem;
		// width: 74%;

		@include bp($point_3, $point_4) {
			margin-bottom: 6rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 5.2rem;
			padding-bottom: 5rem;
		}
	}
	&.testimonials_section__col--case_mod {
		position: relative;
		max-width: 113.9rem;
	}

	&.testimonials_section__col--reviewed_mod {
		margin-left: auto;
		// width: 26%;

		@include bp($point_2, min) {
			padding-top: 2rem;
			max-width: 45rem;
		}

		@include bp($point_3, $point_4) {
			max-width: calc(100% - var(--testimonial-info-col-width) - 5.6rem);
		}

		@include bp($point_4 - 1) {
			margin: 0 calc(var(--gl-indent) * -1);
			width: calc(100% + var(--gl-indent) * 2);
		}
	}
}

//---------------------------------------------testimonials_section###
//---------------------------------------------testimonials_case
.testimonials_case {
	background-color: var(--light_color);

	@include bp($point_2, min) {
		padding-top: 15.6rem;
		padding-bottom: 17.8rem;
	}

	@include bp($point_3, $point_4) {
		padding-top: 10rem;
		padding-bottom: 11.8rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 4.5rem;
		padding-bottom: 6.1rem;
	}
}

.testimonials_case__title {
	color: var(--primary_color);
	font-weight: 600;

	@include bp($point_2, min) {
		font-size: 4.8rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
	}
}

.testimonials_case__head {
	@include flex_block(row, wrap, flex-start);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 9.1rem;
		}
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 7rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}
}

.testimonials_case__row {
	@extend %global_flex_block_row_nowrap_center;

	position: relative;

	@include bp($point_3) {
		flex-wrap: wrap;
	}
}

.testimonial_slider__decor {
	position: absolute;
	top: 0;
	z-index: 2;

	left: var(--testimonial-info-col-width);
	width: calc(100% - var(--testimonial-info-col-width));

	@include bp($point_2, min) {
		height: 9.6rem;
		padding-left: 9.2%;
	}

	@include bp($point_3, $point_4) {
		height: 5.8rem;
		padding-left: 5.6rem;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.testimonial_slider__decor_in {
	position: relative;

	height: 100%;

	border-bottom: solid 1px #b7becc;

	&:before {
		position: absolute;
		left: 0;

		pointer-events: none;

		background: url("../../images/quote_decor.png") no-repeat center center;
		background-size: contain;

		content: "";

		@include bp($point_2, min) {
			top: 2.4rem;

			width: 5.8rem;
			height: 4.6rem;
		}

		@include bp($point_3) {
			top: 0;

			width: 4rem;
			height: 4rem;
		}

		@include bp($point_4 - 1) {
			display: none;
		}
	}
}
//---------------------------------------------testimonials_case###
