@import "ScssHelpers/helpers_all";
//---------------------------------------------testimonial_client
.testimonial_client {
	@include flex_block(row, wrap, flex-start);

	@include bp($point_4 - 1) {
		width: 100%;
	}

	&.testimonial_client--without_more_mod {
		.testimonial_client__col--text_state {
			@include bp($point_2, min) {
				padding-left: 9.2%;
			}
		}
	}
}

.testimonial_client__col {
	&.testimonial_client__col--info_state {
		@include bp($point_4, min) {
			width: var(--testimonial-info-col-width);
		}

		@include bp($point_4 - 1) {
			width: 100%;
			padding-bottom: 2rem;
			margin-bottom: 1.4rem;
			border-bottom: 1px solid var(--gray_color);
		}
	}

	&.testimonial_client__col--text_state {
		flex: 1 1 var(--testimonial-info-col-width);

		@include bp($point_2, min) {
			padding-top: 12rem;
			padding-left: 10%;
		}

		@include bp($point_3, $point_4) {
			padding-left: 5.6rem;
			padding-top: 7.8rem;
		}

		@include bp($point_4 - 1) {
			width: 100%;
		}
	}
}

.testimonial_client__block {
	@include bp($point_4, min) {
		padding-bottom: 1.6rem;
		margin-bottom: 2rem;

		border-bottom: solid 1px #dee6f5;
	}

	@include bp($point_4 - 1) {
		@include flex_block(row, wrap, flex-start);

		margin-bottom: 3rem;
	}
}

.testimonial_client__avatar {
	background-color: #cdd0db;

	@include bp($point_2, min) {
		margin-bottom: 3rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 2.6rem;
	}

	@include bp($point_4, min) {
		width: 9.6rem;
		height: 9.6rem;
	}

	@include bp($point_4 - 1) {
		width: 6.4rem;
		height: 6.4rem;
		// margin-right: 1.6rem;
	}
}

.testimonial_client__avatar_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

.testimonial_client__info {
	@include bp($point_4 - 1) {
		margin-top: 0.4rem;
		padding-left: 1.6rem;
		flex: 1 1 6.4rem;
	}
}

.testimonial_client__name {
	font-weight: 600;
	color: var(--primary_dark_color);
	letter-spacing: -0.07rem;
	line-height: 1;

	// @include flex_block(row, wrap, flex-start, center, center);

	@include bp($point_2, min) {
		font-size: 2.8rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 2.4rem;
	}

	@include bp($point_4, min) {
		margin-bottom: 1.4rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 1rem;
		font-size: 1.8rem;
	}
}

.testimonial_client__position {
	color: #7e92b4;
	font-weight: 500;
	letter-spacing: -0.05rem;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 14rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}

	@include bp($point_4 - 1) {
		max-width: 18rem;
	}
}

.testimonial_client__linkedin {
	display: inline-block;
	vertical-align: middle;
	margin-left: 1.2rem;

	border-radius: 0.4rem;
	overflow: hidden;

	background-color: #0a66c2;
	transition: opacity 0.3s ease;

	@include bp($point_2, min) {
		width: 2.4rem;
		height: 2.4rem;
	}

	@include bp($point_3, $point_4) {
		width: 2rem;
		height: 2rem;

		margin: -0.6rem 0 0 0.8rem;
	}

	@include bp($point_4 - 1) {
		width: 1.8rem;
		height: 1.8rem;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		opacity: 0.6;
	}
}

.testimonial_client__linkedin_icon {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

.testimonial_client__company_wrap {
	@include bp($point_4 - 1) {
		position: relative;

		&:before {
			content: "";

			position: absolute;
			top: 50%;
			left: 0;

			width: 3rem;
			height: 3rem;
			margin-top: -1.5rem;

			background: url("../../images/quote_decor.png") no-repeat center center;
			background-size: contain;
		}
	}
}

.testimonial_client__company {
	display: block;

	transition: opacity 0.3s ease;

	@include bp($point_2, min) {
		height: 4.4rem;
		width: 20.4rem;
	}

	@include bp($point_3, $point_4) {
		height: 3.8rem;
		width: 17.2rem;
	}

	@include bp($point_4 - 1) {
		width: 13.2rem;
		height: 3rem;
		margin-left: auto;
	}

	&:hover,
	&:focus {
		text-decoration: none;
		opacity: 0.7;
	}
}

.testimonial_client__company_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

.testimonial_client__text {
	position: relative;

	height: calc(var(--testimonial-client-text-height) + var(--testimonial-client-footer-height));

	// margin-bottom: 2.2rem;
	// height: 28rem;

	.testimonial_client--without_more_mod & {
		height: auto !important;
	}
}

.testimonial_client__text_container {
	position: absolute;
	top: 0;
	left: 0;

	height: 100%;
	width: 100%;

	.testimonial_client--without_more_mod & {
		position: relative;

		height: auto !important;

		@include bp($point_4, min) {
			&:not(:last-child) {
				margin-bottom: 4.2rem;
			}
		}

		@include bp($point_4 - 1) {
			&:not(:last-child) {
				margin-bottom: 2rem;
			}
		}
	}
}

.testimonial_client__text_in {
	position: relative;

	height: var(--testimonial-client-text-height);

	color: #45649a;
	line-height: 1.5;
	letter-spacing: -0.024rem;

	overflow: hidden;
	transition: height 0.2s ease-out;

	@include d3();

	a {
		&:hover, &:active, &:focus {
			color: #fb3b1e;
			text-decoration: none;
		}
	}

	.testimonial_client--without_more_mod & {
		height: auto;
	}

	@include bp($point_2, min) {
		font-size: 2.4rem;
		line-height: 1.5;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.8rem;
		line-height: 1.45;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		line-height: 1.4;
	}

	br {
		@include bp($point_4 - 1) {
			display: none;
		}
	}

	p {
		&:not(:last-of-type) {
			@include bp($point_4, min) {
				margin-bottom: 1.8rem;
			}

			@include bp($point_4 - 1) {
				margin-bottom: 1.2rem;
			}
		}
	}

	&:before {
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 2;

		width: 100%;
		height: 7.6rem;

		background-image: linear-gradient(to bottom, rgba(250, 251, 252, 0), #fafbfc);
		transition: opacity 0.3s ease;
		pointer-events: none;

		content: "";
	}

	.testimonial_client--without_more_mod & {
		&:before {
			content: none;
		}
	}

	.testimonial_client--active_state & {
		&:before {
			opacity: 0;
		}
	}
}

.testimonial_client__footer {
	position: absolute;
	left: 0;
	bottom: 0;

	height: var(--testimonial-client-footer-height);
	width: 100%;

	transition: opacity 0.3s ease;

	.testimonial_client--without_more_mod & {
		display: none;
	}

	.testimonial_client--active_state & {
		opacity: 0;
		pointer-events: none;
	}

	@include flex_block(row, wrap, flex-start, flex-end, flex-end);
}

.testimonial_client__text_container_in {
	position: relative;
}

.testimonial_client__text_container_decor {
	@include bp($point_3) {
		position: absolute;
		top: 0;
		// top: -10rem;
		right: calc(var(--gl-indent) * -1);
		z-index: -1;

		width: 500vh;

		background-color: var(--light_color);

		// box-shadow: .5rem .5rem 3rem 0 rgba(var(--primary_dark_color), .1);
		box-shadow: 0px 3.5rem 3rem -1.5rem rgba(#cad4e6, 0.5);
		opacity: 0;
		transition: opacity 0.3s ease-in;
		pointer-events: none;

		.testimonial_client--active_state & {
			opacity: 1;
		}
	}

	@include bp($point_3, $point_4) {
		bottom: -6rem;
		// top: -15.2rem;
	}

	@include bp($point_4 - 1) {
		bottom: -4rem;
		// top: -22.2rem;
	}
}

.testimonial_client__text_button {
	@include bp($point_2, min) {
		margin-top: 2.2rem;
	}

	@include bp($point_3) {
		opacity: 0;
		transition: opacity 0.3s ease;

		.testimonial_client--active_state & {
			// height: 100%;
			opacity: 1;
		}
	}

	@include bp($point_3, $point_4) {
		margin-top: 2.6rem;
	}

	@include bp($point_4 - 1) {
		margin-top: 2rem;
	}

	.testimonial_client--without_more_mod & {
		display: none;
	}
}

.testimonial_client_link {
	@include flex_block(row, nowrap, flex-start, center, center);
}

.testimonial_client_link:hover,
.testimonial_client_link:focus {
	text-decoration: none;

	.testimonial_client_link__in {
		color: var(--orange_color);
	}
}

.testimonial_client_link__in {
	@extend %transition_color;

	color: var(--blue_color_2);

	@include bp($point_2, min) {
		font-size: 2.4rem;

		&:not(:last-child) {
			margin-right: 0.9rem;
		}
	}

	@include bp($point_3, $point_4) {
		font-size: 1.8rem;

		&:not(:last-child) {
			margin-right: 0.7rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;

		&:not(:last-child) {
			margin-right: 0.5rem;
		}
	}
}

.testimonial_client_link__icon {
	@include bp($point_2, min) {
		width: 7.1rem;
		height: 2rem;
	}

	@include bp($point_3, $point_4) {
		width: 5.1rem;
		height: 1.5rem;
	}

	@include bp($point_4 - 1) {
		width: 3.6rem;
		height: 1rem;
	}
}

.testimonial_client_link__img {
	@extend %contain_img;
}

//---------------------------------------------testimonial_client###
